.container{
    display: flex;
    height: 100vh;
    width: 100vw;
    position: relative;
}
    .container::before{
        content:"";
        position: absolute;
        z-index: -2;
        height: 100%;
        width: 100%;
        background: center / cover no-repeat url(../../assets/background.jpg) ;
    }
    .container::after{
        content:"";
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: #000;
        opacity: .5;
    }

